export default {
     /**
     * 
     */
    searchedPersons: (state) => {
        let {Persons, searchVal} = state;
        return Persons.filter((person)=>{
            return !person.choosed && person.nodeDesc.indexOf(searchVal)
        });
    },

    characters: (state) => {
        let {Persons} = state;
    },
    //
    exceedMax: (state) => this.choosedPersons.length > state.maxNum
}



